import React from "react";
import { Link } from "react-router-dom";
import { featured } from "../data/Data";

const HazardsCard = () => {
  // Count the number of hazards for each type
  const hazardCounts = featured.reduce((counts, hazard) => {
    counts[hazard.type] = (counts[hazard.type] || 0) + 1;
    return counts;
  }, {});

  return (
    <div className="content grid4 mtop">
    {/* Render the key above the hazard list */}
    <div className='key' style={{ marginTop: '20px', marginBottom: '20px' }}>
      <h4>Key</h4>
      {/* Display hazard types and their counts */}
      {Object.entries(hazardCounts).map(([type, count]) => (
        <span key={type} style={{ color: type === 'General Mining Hazards' ? 'blue' : type === 'Surface Hazards' ? 'brown' : 'yellow' }}>
          {type} ({count}) &nbsp;
        </span>
      ))}
    </div>
    <div></div>
    <div></div>
    <div></div>
      
      {/* Render hazards */}
      {featured.map((hazard, index) => (
        <div className="content grid3 mtop" key={index} >
          <Link to={`/hazard-details/${hazard.hazards}`} className='box-link' >
            <div className='box' style={{ border: `3px double ${hazard.color}` }}>
              <ul>
                <li>{hazard.hazards}</li>
              </ul> 
            </div>
          </Link>
        </div>
      ))}
    </div>
  );
};


export default HazardsCard;
