import React, { useState } from 'react';
import { featured } from '../../data/Data';
import Heading from "../../common/Heading"
import "./hero.css"

const Hero = () => {
  const [location, setLocation] = useState('');
  const [type, setHazardType] = useState('');
  const [status, setStatus] = useState('');

  const handleSubmit = (e) => {
    e.preventDefault();


    const filteredData = featured.filter(item => {
      return (
        //item.location.toLowerCase().includes(location.toLowerCase()) &&
        item.type.toLowerCase().includes(type.toLowerCase()) &&
        item.status.toLowerCase().includes(status.toLowerCase())
      );
    });

    // Do something with the filtered data
    console.log(filteredData);
  };
  return (
    <>
      <section className='hero'>
        <div className='container'>
          <Heading title='Hazard Database ' subtitle='View alerts and hazards for your organisation' />

          <form className='flex' onSubmit={handleSubmit}>
            <div className='box'>
              <span>Location</span>
              <input type='text' placeholder='Location'   />
            </div>
            <div className='box'>
              <span>Hazard Type</span>
              <input type='text' placeholder='Hazard Type'
              value={type} 
              onChange={(e) => setHazardType(e.target.value)}  />
            </div>
            <div className='box'>
              <span>Status</span>
              <input type='text' placeholder='Status'  value={status} 
               onChange={(e) => setStatus(e.target.value)} />
            </div>
            <div className='box'>
              <h4>Advance Filter</h4>
            </div>
            <button className='btn1'>
              <i className='fa fa-search'></i>
            </button>
          </form>
        </div>
      </section>
    </>
  )
}

export default Hero
