import React, { useState } from 'react';
import axios from 'axios';
import { useHistory } from 'react-router-dom';
import 'bootstrap/dist/css/bootstrap.min.css';

const Login = ({ setUser }) => {
    const [username, setUsername] = useState('');
    const [password, setPassword] = useState('');
    const [message, setMessage] = useState('');
    const history = useHistory();

    const handleLogin = async (e) => {
        e.preventDefault();
        try {
            const response = await axios.post('/api/auth/login', {
                username,
                password,
            });
            // Assuming the user data is in response.data.user
            localStorage.setItem('user', JSON.stringify(response.data.user));
            setUser(response.data.user);
            setMessage('Login successful');
            history.push('/'); // Redirect to the home page or another route
        } catch (error) {
            if (error.response && error.response.data.errors) {
                setMessage(error.response.data.errors.map(err => err.msg).join(', '));
            } else if (error.response && error.response.data.message) {
                setMessage(error.response.data.message);
            } else {
                setMessage('Login failed');
            }
        }
    };

    const handleRedirectToRegister = () => {
        history.push('/register');
    };

    return (
        <div className="container mt-5">
            <form className="login-form" onSubmit={handleLogin}>
                <h2 className="text-center">Login</h2>
                <div className="form-group">
                    <input
                        type="text"
                        className="form-control"
                        placeholder="Username"
                        value={username}
                        onChange={(e) => setUsername(e.target.value)}
                        required
                    />
                </div>
                <div className="form-group">
                    <input
                        type="password"
                        className="form-control"
                        placeholder="Password"
                        value={password}
                        onChange={(e) => setPassword(e.target.value)}
                        required
                    />
                </div>
                <button className="btn btn-primary btn-block" type="submit">Login</button>
                <button className="btn btn-link btn-block" type="button" onClick={handleRedirectToRegister}>Register</button>
            </form>
            {message && <p className="text-center mt-3">{message}</p>}
        </div>
    );
};

export default Login;
