import React, { useState } from 'react';
import './header.css';
import { nav } from '../../data/Data';
import { Link, useHistory } from 'react-router-dom';

const Header = ({ user, setUser }) => {
  const history = useHistory();
  const [navList, setNavList] = useState(false);

  const handleLoginRedirect = () => {
    history.push('/login');
  };

  const handleLogout = () => {
    localStorage.removeItem('user');
    setUser(null);
    history.push('/');
  };

  return (
    <header>
      <div className='container d-flex justify-content-between align-items-center'>
        <div className='logo'>
          {/* <img src='./images/logo.png' alt='' /> */}
        </div>
        <div className='nav'>
          <ul className={`nav ${navList ? 'flex-column' : 'flex-row'}`}>
            {nav.map((list, index) => (
              <li key={index} className='nav-item'>
                <Link to={list.path} className='nav-link'>{list.text}</Link>
              </li>
            ))}
          </ul>
        </div>
        <div className='button d-flex align-items-center'>
          <h4>
            <span>0</span> Alerts
          </h4>
          {user ? (
            <div className='user-info ml-3 d-flex align-items-center'>
              <span>Welcome, {user.username}</span>
              <button className='btn btn-danger ml-2' onClick={handleLogout}>
                <i className='fa fa-sign-out'></i> Sign Out
              </button>
            </div>
          ) : (
            <button className='btn btn-primary ml-2' onClick={handleLoginRedirect}>
              <i className='fa fa-sign-in'></i> Sign In
            </button>
          )}
        </div>
        <div className='toggle d-block d-md-none'>
          <button onClick={() => setNavList(!navList)}>
            {navList ? <i className='fa fa-times'></i> : <i className='fa fa-bars'></i>}
          </button>
        </div>
      </div>
    </header>
  );
};

export default Header;
