import React from "react";
import { footer } from "../../data/Data";
import "./footer.css";

const Footer = () => {
  return (
    <>
      <section className="footerContact py-5 bg-dark text-light">
        <div className="container">
          <div className="row justify-content-center">
            <div className="col-md-8">
              <div className="text-center">
                <p>Have a record of all hazards.</p>
              </div>
            </div>
          </div>
        </div>
      </section>

      <footer className="py-5">
        <div className="container">
          <div className="row">
            {footer.map((val, index) => (
              <div key={index} className="col-md-4 mb-4">
                <h3>{val.title}</h3>
                <ul className="list-unstyled">
                  {val.text.map((items, i) => (
                    <li key={i}>{items.list}</li>
                  ))}
                </ul>
              </div>
            ))}
          </div>
        </div>
      </footer>
      <div className="bg-dark text-light py-3 text-center">
        <span> 2023 GIS. Designed By KGOMOTSO.</span>
      </div>
    </>
  );
};

export default Footer;
