import React, { useState, useEffect } from 'react';
import { useParams } from 'react-router-dom';
import { Link } from "react-router-dom";
import { featured } from '../data/Data';
import './hazards.css';

const HazardDetails = () => {
  const { id } = useParams();
  const [hazardData, setHazardData] = useState(null);

  useEffect(() => {
    const selectedHazard = featured.find(hazard => hazard.hazards === id);
    setHazardData(selectedHazard);
  }, [id]);

  return (
    <div>
      <h2>Hazard Details</h2>
      <h4>{id}</h4>
      <Link to={`/hierachy/${id}`}  className='box-link'>
          <div>
           <button className="button-secondary" >Go to Hierachy of Controls</button >
          </div>
        </Link>
      {hazardData ? (
        <div>
          <table className="table-bordered">
            <thead>
              <tr>
                <th>Risk</th>
                <th>RiskFactor/Mechanism</th>
                <th>Control</th>
                <th>Further Information</th>
              </tr>
            </thead>
            <tbody>
              {Object.entries(hazardData.Risk).map(([riskKey, riskData], index) => (
                <React.Fragment key={index}>
                  <tr>
                    <td>{riskKey}</td>
                    <td>
                      <ul>
                        {riskData.riskFactors && riskData.riskFactors.map((factor, factorIndex) => (
                          <li key={factorIndex}>{factor.riskFactor}</li>
                        ))}
                      </ul>
                    </td>
                    <td>
                      <ul>
                        {riskData.controls && riskData.controls.map((control, controlIndex) => (
                          <li key={controlIndex} style={{ borderBottom: '1px solid #dddddd', padding: '6px' }}>{control.control}</li>
                        ))}
                      </ul>
                    </td>
                    <td>
                      <ul>
                        {riskData.furtherInformation && riskData.furtherInformation.map((info, infoIndex) => (
                          <li key={infoIndex}><a href={info.information} target="_blank" rel="noopener noreferrer" style={{ borderBottom: '1px solid #dddddd', padding: '6px' }}>{info.information}</a></li>
                        ))}
                      </ul>
                    </td>
                  </tr>
                </React.Fragment>
              ))}
            </tbody>
          </table>
        </div>
      ) : (
        <p>No data available for the selected hazard.</p>
      )}
    </div>
  );
};


export default HazardDetails;
