//import Back from "../common/Back"
import "../home/featured/Featured.css"
import React from "react";
//import React, { useState, useEffect } from 'react';
//import { featured } from "../data/Data";
//import { Link } from "react-router-dom";
import HazardsCard from "./hazardsCard";
//import hazardsCard from "./hazardsCard"

function HazardList({hazards}) {
  //const hazards = featured[0].hazards;
  return (
   
    <>
      <section className='featured background'>
         <div className='container'>
         <h2 className="center">Hazards</h2>
           <HazardsCard/>
         </div>
      </section>
    </>
  );
}

function HazardDetails({ hazard, onClose }) {
  return (
    <div>
      <h2>{hazard.type}</h2>
      <p>Details about the hazard go here...</p>
      <button onClick={onClose}>Close</button>
    </div>
  );
}

function Hazards() {
  const [selectedHazard, setSelectedHazard] = React.useState(null);

  const handleSelectHazard = (hazard) => {
    setSelectedHazard(hazard);
  };

  const handleCloseDetails = () => {
    setSelectedHazard(null);
  };

  return (
    <div>
      {!selectedHazard ? (
        <HazardList onSelectHazard={handleSelectHazard} />
      ) : (
        <HazardDetails hazard={selectedHazard} onClose={handleCloseDetails} />
      )}
    </div>
  );
}

export default Hazards
