// Dashboard.js
import React, { useState, useEffect } from 'react';
import axios from 'axios';

const Dashboard = () => {
    const [alerts, setAlerts] = useState([]);
    const [riskControls, setRiskControls] = useState([]);
    const [hazards, setHazards] = useState([]);

    useEffect(() => {
        // Fetch data for alerts
        axios.get('/api/alerts')
            .then(response => setAlerts(response.data))
            .catch(error => console.error('Error fetching alerts:', error));

        // Fetch data for risk controls
        axios.get('/api/risk-controls')
            .then(response => setRiskControls(response.data))
            .catch(error => console.error('Error fetching risk controls:', error));

        // Fetch data for hazards
        axios.get('/api/hazards')
            .then(response => setHazards(response.data))
            .catch(error => console.error('Error fetching hazards:', error));
    }, []);

    return (
        <div className="dashboard">
            <h1>Dashboard</h1>
            {/* Display alerts */}
            <div className="alerts">
                <h2>Alerts</h2>
                {alerts.map(alert => (
                    <div key={alert.id}>{alert.title}</div>
                ))}
            </div>
            {/* Display risk controls */}
            <div className="risk-controls">
                <h2>Risk Controls</h2>
                {riskControls.map(control => (
                    <div key={control.id}>{control.name}</div>
                ))}
            </div>
            {/* Display hazards */}
            <div className="hazards">
                <h2>Hazards</h2>
                {hazards.map(hazard => (
                    <div key={hazard.id}>{hazard.name}</div>
                ))}
            </div>
        </div>
    );
};

export default Dashboard;
