import React from 'react'
import { Link } from "react-router-dom";
import { featured } from '../data/Data';

const GeneralMiningHazards = () => {
  // Filter the hazards to get only the ones with type "General Mining Hazards"
  const generalHazards = featured.filter(hazard => hazard.type === 'General Mining Hazards');

  return (
    <div>
      <h2>General Mining Hazards</h2>
      <ul>
        {generalHazards.map((hazard, index) => (
        <div className="content grid5 mtop" key={index} > 
        <Link to={`/hazard-details/${hazard.hazards}`} className='box-link' >  
        <div className='box' style={{ border: `5px double ${hazard.color}` }}> 
          <ul>
            <li>{hazard.hazards}</li>
        </ul>
        </div>
          </Link>
          </div>  
        ))}
      </ul>
    </div>
  );
};

export default GeneralMiningHazards;