import React, { useState, useEffect } from "react";
import Header from "../common/header/Header";
import { BrowserRouter as Router, Switch, Route } from "react-router-dom";
import Home from "../home/Home";
import Footer from "../common/footer/Footer";
import About from "../about/About";
import Pricing from "../pricing/Pricing";
import Blog from "../blog/Blog";
import Hazards from "../hazards/Hazards";
import Contact from "../contact/Contact";
import HazardDetails from "../hazards/hazardDetails";
import GeneralMiningHazards from "../hazardTypes/GeneralMiningHazards";
import { Hierachy } from "../hazards/Hierachy";
import { Create } from "../SafetyBulletin/Create";
import Login from "../UserManagement/Login";
import Register from "../UserManagement/Register";
import Dashboard from "../Dashboard/Dashboard";

const Pages = () => {
  const [user, setUser] = useState(null);

  useEffect(() => {
    // Check if user is stored in localStorage
    const storedUser = localStorage.getItem('user');
    if (storedUser) {
      try {
        setUser(JSON.parse(storedUser));
      } catch (e) {
        console.error("Failed to parse user from localStorage:", e);
        setUser(null);
      }
    }
  }, []);

  return (
    <Router>
      <Header user={user} setUser={setUser} />
      <Switch>
        <Route exact path="/" component={Home} />
        <Route exact path="/about" component={About} />
        <Route exact path="/hazards" component={Hazards} />
        <Route exact path="/hazard-details/:id" component={HazardDetails} />
        <Route exact path="/generalmining" component={GeneralMiningHazards} />
        <Route exact path="/blog" component={Blog} />
        <Route exact path="/pricing" component={Pricing} />
        <Route exact path="/contact" component={Contact} />
        <Route exact path="/hierachy/:id" component={Hierachy} />
        <Route exact path="/SafetyBulletin" component={Create} />
        <Route exact path="/dashboard">
          <Dashboard user={user} />
        </Route>
        <Route exact path="/login">
          <Login setUser={setUser} />
        </Route>
        <Route exact path="/register" component={Register} />
      </Switch>
      <Footer />
    </Router>
  );
};

export default Pages;
