import React from 'react'
import { Link } from "react-router-dom";
import { useParams } from 'react-router-dom';



export const Hierachy = () => {
  const { id } = useParams();
  return (
    <>
       <div>
       <Link to={`/hazards`}  className='box-link'>
          <div>
           <button className="btn" >Go to Hazards</button >
          </div>
        </Link>

        <Link to={`/hazard-details/${id}`}  className='box-link'>
          <div>
           <button className="btn" >Return to hazard</button >
          </div>
        </Link>
       
       </div>
      <div>Hierachy</div>
      <img src="../hierachy.png" alt='hierachys' />
    </>
    
  )
}
